import Vue from "vue";

export function oneOf(value, validList) {
  for (let i = 0; i < validList.length; i++) {
    if (value === validList[i]) {
      return true;
    }
  }
  return false;
}

/**
 * @description 
 * @param {Function} func 
 * @param {number} wait 
 * @param {string} name window
 */
export function debounce(func, wait, name) {
  if (window[name]) clearTimeout(window[name]);
  window[name] = setTimeout(function() {
    func();
    window[name] = undefined;
  }, wait);
}

/**
 * @description 
 * @param {string} str 
 * @returns
 */
export function toHump(str) {
  return str.replace(/\-(\w)/g, function(all, letter) {
    return letter.toUpperCase();
  });
}

/**
 * @description 
 * @param {string} str 
 * @returns
 */
export function toLine(str) {
  return str.replace(/([A-Z])/g, "-$1").toLowerCase();
}

/**
 * @description VNode
 * @param {HTMLNode} node dom
 * @returns boolean
 */
export function isVNode(node) {
  return (
    node !== null &&
    typeof node === "object" &&
    hasOwnProperty.call(node, "componentOptions")
  );
}

/**
 * @description 
 * @param {*} obj 
 * @returns {string} 
 */
function typeOf(obj) {
  const toString = Object.prototype.toString;
  const map = {
    "[object Boolean]": "boolean",
    "[object Number]": "number",
    "[object String]": "string",
    "[object Function]": "function",
    "[object Array]": "array",
    "[object Date]": "date",
    "[object RegExp]": "regExp",
    "[object Undefined]": "undefined",
    "[object Null]": "null",
    "[object Object]": "object"
  };
  return map[toString.call(obj)];
}

/**
 * @description 
 * @param {object} data 
 * @returns {object} 
 */
export function deepCopy(data) {
  const t = typeOf(data);
  let o;

  if (t === "array") {
    o = [];
  } else if (t === "object") {
    o = {};
  } else {
    return data;
  }

  if (t === "array") {
    for (let i = 0; i < data.length; i++) {
      o.push(deepCopy(data[i]));
    }
  } else if (t === "object") {
    for (let i in data) {
      o[i] = deepCopy(data[i]);
    }
  }
  return o;
}

/**
 * @description 
 * @param {string} fmt 
 * @param {Date} date 
 * @returns
 */
export function dateFormat(fmt, date) {
  let ret;
  const opt = {
    "Y+": date.getFullYear().toString(), // 
    "m+": (date.getMonth() + 1).toString(), // 
    "d+": date.getDate().toString(), // 
    "H+": date.getHours().toString(), // 
    "M+": date.getMinutes().toString(), // 
    "S+": date.getSeconds().toString() // 
    // 
  };
  for (let k in opt) {
    ret = new RegExp("(" + k + ")").exec(fmt);
    if (ret) {
      fmt = fmt.replace(
        ret[1],
        ret[1].length == 1 ? opt[k] : opt[k].padStart(ret[1].length, "0")
      );
    }
  }
  return fmt;
}
